@include tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog {
    margin-bottom: 2rem;

    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    &:last-of-type {
        margin-bottom: 0;
    }
}

.informacion-meta{
    span {
        color: $amarillo;
    }
}

.texto-entrada {

    a {
        color: $negro;
        margin-top: 2rem; //no agarra margen por q es de tipo inline
        display: block;

        @include tablet {
            margin-top: 0;
        }
    }

    h4 {
        margin: 0;
        line-height: 1.4;

        &::after {
            //crear la linea verde
            content: '';
            display: block; //no se veria la linea
            width: 15rem;
            height: .5rem;
            background-color: $verde;
        }
    }

   
}


